
import React, { useEffect, useState } from 'react'
import './boddy.css'


export default function Boddy(){
   const[ title, setTitle ] = useState('')
   const[ city, setCity ] = useState('')
   
   useEffect(()=>{
      fetch(`https://goweather.herokuapp.com/weather/${title}`)
      .then(res =>res.json()).then(response => {
   setCity(response) 
      })
   },[title])

  useEffect(()=>{
      fetch(`https://goweather.herokuapp.com/weather/Paris`)
      .then(res =>res.json()).then(response => {
   setCity(response) 
      })
   },[title])

  let temperature = ''
  let wind = ''
  let description = ''
  let forecast = ''

if(city !==undefined){
   temperature = city.temperature;
   wind = city.wind;
   description = city.description;
   forecast = city.forecast
  }
  
   return(
      <div className='main'>
         <input type='text' placeholder='enter city name  (e.g. Paris)' value={title} onChange={ (evt) => setTitle (evt.target.value) } />

<div className='top'>
   <div className = 'today'>
            <h1 className='title' > Wheather for Today </h1>
                  <h3> temperature: {temperature} </h3> 
                  <h3> wind: {wind} </h3>
                  <h3> description: {description} </h3>
   </div>
                     <h1>Coming Days:</h1>
           {title ? city.forecast.map (item => {
             return(
               <div className = 'nextDays'> 
                     
                  <h3> Day { item.day }. </h3> 
                  <h3> temperature: { item.temperature } </h3> 
                  <h3> wind: { item.wind } </h3> 
               </div>
                )
         }) : "" }

         
</div>
         
      </div>   
   )
}